.card__stars {
  display: flex;
  justify-content: space-around;
  &--4 :nth-child(-n+4) {
    background-image: url("../images/star-active.svg");
  }
}

.card__star {
  display: flex;
  height: 16px;
  width: 16px;
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  &:not(:last-child) {
    margin-right: 4px;
  }
}
